.page-header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.signup__image {
  border-radius: 50%;
}

.signup__btn {
  background-color: #fcfcfc;
  border: none;
}

body.dark .signup__btn {
  background-color: #202025;
}

.signup-greeting {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.signup-greeting.is-open {
  opacity: 1;
  visibility: inherit;
  z-index: 7;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  flex-direction: column;
  padding: 5px;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.signup-greeting__text {
  padding-bottom: 5px;
}

.signup-greeting__logout {
  color: #fd5103;
  background-color: #fcfcfc;
  border-color: #fd5103;
  border-radius: 4px;
  padding: 3px;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
}

.signup-greeting__logout:not(:last-child) {
  margin-bottom: 8px;
}

body.dark .signup-greeting__logout {
  color: #fd5103;
  background-color: #202025;
}

.signup-greeting__logout:hover {
  color: #fcfcfc;
  background-color: #fd5103;
  border-color: #fd5103;
}

body.dark .signup-greeting__logout:hover {
  color: #fd5103;
  background-color: #fcfcfc;
  border-color: #fd5103;
}

.header-switcher {
  align-items: center;
  gap: 10px;
  display: flex;
}

.header-switcher__item-light {
  color: #fd5103;
  font-size: 18px;
  line-height: 24px;
}

body.dark .header-switcher__item-light {
  color: #fcfcfc;
}

.header-switcher__item-dark {
  color: #5f6775;
  font-size: 18px;
  line-height: 24px;
}

body.dark .header-switcher__item-dark {
  color: #fd5103;
}

.header-switcher__switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header-switcher__input {
  opacity: 0;
  width: 0;
  height: 0;
}

.header-switcher__input:checked + .header-switcher__slider {
  background-color: #fd5103;
  border: 1px solid #fcfcfc;
  border-radius: 20px;
}

.header-switcher__input:checked + .header-switcher__slider:before {
  background-color: #fcfcfc;
  transform: translate(20px);
}

.header-switcher__input:checked .header-switcher__item-dark {
  color: #fcfcfc;
}

.header-switcher__slider.round {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #fcfcfc;
  border: 1px solid #fd5103;
  border-radius: 20px;
  position: absolute;
}

.header-switcher__slider.round:before {
  width: 12px;
  height: 12px;
  background-color: #fd5103;
  border-radius: 50%;
}

.header-switcher__slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: #fcfcfc;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 2px;
}

.form-submit {
  width: 100%;
  max-width: 288px;
  height: 40px;
  transition-property: outline;
  transition-duration: 350ms cubic-bezier(.4, 0, .2, 1);
  border: 1px solid #fd5103;
  border-radius: 4px;
  align-items: center;
  margin-right: 40px;
  padding: 8px;
  display: flex;
}

.form-submit__input {
  max-width: 247px;
  transition-duration: 350ms cubic-bezier(.4, 0, .2, 1);
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.form-submit__button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-items: center;
  padding: 0% 8px 0% 0%;
  display: flex;
}

.form-submit__input {
  height: 24px;
  width: 99%;
  color: #5f6775;
  margin-left: 2px;
  padding-left: 8px;
  font-size: 14px;
}

.form-submit__input:focus {
  outline-offset: 0;
  outline: 0;
}

.line {
  width: 1px;
  height: 40px;
  background-color: #fd5103;
}

input::-ms-input-placeholder {
