.spinner {
  margin: 0 auto;
  // display: block;
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}
.spinner:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid $accent-color;
  border-color: $accent-color transparent $accent-color transparent;
  animation: spinnerAnimation 900ms linear infinite;
}
@keyframes spinnerAnimation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
